/*banner mail-1280_250*/
.banner{
  &__mail-1920_250{
    width: 1920px;
    height: 250px;
  }

  &__body-mail-1920_250{
    padding-top:81px;
    padding-left: 56px;
    padding-right: 15px;
    padding-bottom: 16px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 60px;
      line-height: 56px;
    }
    .banner__description{
      margin-left: auto;
      width: 100%;
      max-width: 787px;
      font-size: 42px;
      line-height: 44px;
      margin-right: 82px;
    }
    .banner__footnote{
      display: flex;
      margin-left: auto;
      margin-top: auto;
      margin-right: 6px;
      p{
        &:not(:last-child){
          margin-right: 16px;
        }
        text-align: right;
        font-size: 16px;
        line-height: 19px;
        &:nth-child(2){
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 300px;
      height: 300px;
      top: 4px;
      left: 50%;
      transform: translateX(-103%) rotate(-90deg);
    }
    .banner__img{
      width: 360px;
      top: -33px;
      left: -2px;
    }

  }
}
