/*banner mail-290_170*/
.banner{
  &__mail-290_170{
    width: 290px;
    height: 170px;
  }
  &__body-mail-290_170{
    padding-top:10px;
    padding-left: 16px;
    padding-right: 10px;
    padding-bottom: 16px;
    .banner__footnote{
      margin-left: auto;
      p{
        text-align: right;
        font-size: 6px;
        line-height: 7px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__footnote-additional{
      margin-top: auto;
      p{
        font-size: 8px;
        line-height: 9px;
      }

    }
    .banner__title{
      margin-top: 7px;
      font-size: 18px;
      line-height: 18px;
    }
    .banner__description{
      width: 100%;
      max-width: 140px;
      margin-top: 12px;
      font-size: 14px;
      line-height: 15px;
    }
    .banner__container-img{
      width: 120px;
      height: 120px;
      bottom: 2px;
      right: 0;
    }
    .banner__img{
      width: 156px;
      top: -27px;
      left: -14px;
    }
  }
}
