/*banner rcy-640_134_2*/
.banner{
  &__rcy-640_134_2{
    width: 640px;
    height: 134px;
  }

  &__body-rcy-640_134_2{
    padding-top:43px;
    padding-left: 40px;
    padding-right: 15px;
    padding-bottom: 10px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 28px;
      line-height: 28px;
      width: 100%;
      max-width: 136px;
    }
    .banner__description{
      margin-left: auto;
      width: 100%;
      max-width: 220px;
      font-size: 16px;
      line-height: 16px;
      margin-right: 1px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: auto;
      p{
        text-align: right;
        font-size: 8px;
        line-height: 9px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 160px;
      height: 160px;
      top: 5px;
      left: 50%;
      transform: translateX(-65%) rotate(-90deg);
    }
    .banner__img{
      width: 172px;
      top: 19px;
      left: 11px;
    }

  }
}
