/*banner rcy-300_300*/
.banner{
  &__rcy-300_300{
    width: 300px;
    height: 300px;
  }
  &__body-rcy-300_300{
    padding-top:32px;
    padding-left: 20px;
    padding-right: 29px;
    padding-bottom: 20px;
    .banner__footnote{
      margin-top: auto;
      p{
        font-size: 8px;
        line-height: 9px;
      }

    }
    .banner__title{
      font-size: 22px;
      line-height: 20px;
    }
    .banner__description{
      width: 100%;
      max-width: 251px;
      margin-top: 12px;
      font-size: 18px;
      line-height: 19px;
    }
    .banner__container-img{
      width: 189px;
      height: 189px;
      bottom: 9px;
      right: 11px;
    }
    .banner__img{
      width: 220px;
      top: -7px;
      left: -21px;
    }

  }
}
