/*banner rcy-300_500*/
.banner{
  &__rcy-300_500{
    width: 300px;
    height: 500px;
  }
  &__body-rcy-300_500{
    padding-top:20px;
    padding-left: 20px;
    padding-right: 15px;
    padding-bottom: 20px;
    .banner__footnote{
      p{
        font-size: 12px;
        line-height: 14px;
        &:last-child{
          font-size: 11px;
          line-height: 13px;
        }
      }

    }
    .banner__title{
      margin-top: 41px;
      font-size: 27px;
      line-height: 28px;
    }
    .banner__description{
      width: 100%;
      max-width: 265px;
      margin-top: 12px;
      font-size: 19px;
      line-height: 21px;
    }
    .banner__container-img{
      width: 260px;
      height: 260px;
      bottom: 12px;
      right: 18px;
    }
    .banner__img{
      width: 259px;
      top: 7px;
      left: -8px;
    }

  }
}
