/*banner mail-300_300*/
.banner{
  &__mail-300_300{
    width: 300px;
    height: 300px;
  }
  &__body-mail-300_300{
    padding-top:32px;
    padding-left: 20px;
    padding-right: 10px;
    padding-bottom: 8px;

    .banner__footnote{
      margin-top: auto;
      p{
        font-size: 8px;
        line-height: 9px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }

    .banner__title{
      font-size: 22px;
      line-height: 20px;
    }
    .banner__description{
      width: 100%;
      max-width: 251px;
      margin-top: 12px;
      font-size: 18px;
      line-height: 19px;
    }
    .banner__container-img{
      width: 189px;
      height: 189px;
      bottom: 8px;
      right: 11px;
    }
    .banner__img{
      width: 220px;
      top: -6px;
      left: -24px;
    }
  }
}
