/*banner rcy-300_250*/
.banner{
  &__rcy-300_250{
    width: 300px;
    height: 250px;
  }
  &__body-rcy-300_250{
    padding-top:32px;
    padding-left: 20px;
    padding-right: 11px;
    padding-bottom: 20px;
    .banner__footnote{
      margin-top: auto;
      p{
        font-size: 8px;
        line-height: 9px;
      }

    }
    .banner__title{
      font-size: 19px;
      line-height: 20px;
    }
    .banner__description{
      width: 100%;
      max-width: 210px;
      margin-top: 10px;
      font-size: 15px;
      line-height: 17px;
    }
    .banner__container-img{
      width: 160px;
      height: 160px;
      bottom: 4px;
      right: 17px;
    }
    .banner__img{
      width: 190px;
      top: -5px;
      left: -13px;
    }

  }
}
