/*banner rcy-480_320*/
.banner{
  &__rcy-480_320{
    width: 480px;
    height: 320px;
  }
  &__body-rcy-480_320{
    padding-top:32px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
    .banner__footnote{
      margin-top: auto;
      p{
        font-size: 12px;
        line-height: 14px;
      }

    }
    .banner__title{
      font-size: 26px;
      line-height: 20px;
    }
    .banner__description{
      width: 100%;
      max-width: 278px;
      margin-top: 16px;
      font-size: 20px;
      line-height: 22px;
    }
    .banner__container-img{
      width: 260px;
      height: 260px;
      bottom: 8px;
      right: 21px;
    }
    .banner__img{
      width: 307px;
      top: -1px;
      left: -36px;
    }

  }
}
