/*banner rcy-300_600*/
.banner{
  &__rcy-300_600{
    width: 300px;
    height: 600px;
  }
  &__body-rcy-300_600{
    padding-top:20px;
    padding-left: 20px;
    padding-right: 15px;
    .banner__footnote{
      p{
        font-size: 12px;
        line-height: 14px;
      }

    }
    .banner__title{
      margin-top: 64px;
      font-size: 29px;
      line-height: 28px;
    }
    .banner__description{
      width: 100%;
      max-width: 265px;
      margin-top: 20px;
      font-size: 19px;
      line-height: 21px;
    }
    .banner__container-img{
      width: 280px;
      height: 280px;
      bottom: 27px;
      right: 6px;
    }
    .banner__img{
      width: 292px;
      top: -3px;
      left: -1px;
    }

  }
}
