/*banner mail-290_330*/
.banner{
  &__mail-290_330{
    width: 290px;
    height: 330px;
  }
  &__body-mail-290_330{
    padding-top:16px;
    padding-left: 20px;
    padding-right: 16px;
    padding-bottom: 15px;

    .banner__footnote{
      margin-left: auto;
      p{
        text-align: right;
        font-size: 8px;
        line-height: 9px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__footnote-additional{
      margin-top: auto;
      p{
        font-size: 8px;
        line-height: 9px;
      }

    }
    .banner__title{
      margin-top: 37px;
      font-size: 24px;
      line-height: 24px;
    }
    .banner__description{
      width: 100%;
      max-width: 180px;
      margin-top: 12px;
      font-size: 18px;
      line-height: 20px;
    }
    .banner__container-img{
      width: 166px;
      height: 166px;
      bottom: -6px;
      right: 0;
    }
    .banner__img{
      width: 208px;
      top: -30px;
      left: -8px;
    }
  }
}
