/*banner mail-600_600*/
.banner{
  &__mail-600_600{
    width: 600px;
    height: 600px;
  }
  &__body-mail-600_600{
    padding-top:24px;
    padding-left: 20px;
    padding-right: 40px;
    padding-bottom: 15px;

    .banner__footnote{
      margin-left: auto;
      p{
        text-align: right;
        font-size: 17px;
        line-height: 20px;
        &:nth-child(2){
          span{
            text-transform: uppercase;
          }
        }
      }
    }

    .banner__container-img{
      width: 500px;
      height: 500px;
      bottom: 0;
      right: 41px;
    }
    .banner__img{
      width: 509px;
      top: 17px;
      left: -22px;
    }
  }
}
