/*banner rcy-336_280*/
.banner{
  &__rcy-336_280{
    width: 336px;
    height: 280px;
  }
  &__body-rcy-336_280{
    padding-top:32px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 9px;
    .banner__footnote{
      margin-top: auto;
      p{
        font-size: 8px;
        line-height: 9px;
      }

    }
    .banner__title{
      font-size: 22px;
      line-height: 20px;
    }
    .banner__description{
      width: 100%;
      max-width: 251px;
      margin-top: 12px;
      font-size: 18px;
      line-height: 19px;
    }
    .banner__container-img{
      width: 189px;
      height: 189px;
      bottom: 9px;
      right: 11px;
    }
    .banner__img{
      width: 226px;
      top: -11px;
      left: -25px;
    }

  }
}
