/*banner rcy-320_50*/
.banner{
  &__rcy-320_50{
    width: 320px;
    height: 50px;
  }

  &__body-rcy-320_50{
    padding-top:13px;
    padding-left: 20px;
    padding-right: 4px;
    padding-bottom: 3px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 14px;
      line-height: 14px;
      width: 100%;
      max-width: 68px;
    }
    .banner__description{
      margin-left: auto;
      margin-right: 20px;
      width: 100%;
      max-width: 110px;
      font-size: 8px;
      line-height: 8px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: 0px;
      p{
        text-align: right;
        font-size: 3px;
        line-height: 4px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 60px;
      height: 60px;
      top: 2px;
      left: 50%;
      transform: translateX(-83%) rotate(-90deg);
    }
    .banner__img{
      width: 76px;
      top: 3px;
      left: -4px;
    }

  }
}
