/*banner rcy-1456_180*/
.banner{
  &__rcy-1456_180{
    width: 1456px;
    height: 180px;
  }

  &__body-rcy-1456_180{
    padding-top:20px;
    padding-left: 40px;
    padding-right: 10px;

    .banner__flex-row{
      margin-top: 24px;
    }
    .banner__title{
      font-size: 56px;
      line-height: 46px;
    }
    .banner__description{
      margin-right: 5%;
      margin-left: auto;
      width: 100%;
      max-width: 531px;
      font-size: 28px;
      line-height: 30px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: 30px;
      p{
        font-size: 10px;
        line-height: 12px;
      }
    }
    .banner__container-img{
      width: 240px;
      height: 240px;
      top: 6px;
      left: 50%;
      transform: translateX(-78%) rotate(-90deg);
    }
    .banner__img{
      width: 272px;
      top: 27px;
      left: 20px;
    }

  }
  &__lik-dom{
    margin-left: auto;
    margin-right: 10px;
    font-family: $ff-medium;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.015em;
    text-transform: uppercase;
    color: #686D82;
  }
}
