/*banner rcy-320_100*/
.banner{
  &__rcy-320_100{
    width: 320px;
    height: 100px;
  }

  &__body-rcy-320_100{
    padding-top:30px;
    padding-left: 10px;
    padding-right: 11px;
    padding-bottom: 6px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 18px;
      line-height: 18px;
      width: 100%;
      max-width: 87px;
    }
    .banner__description{
      margin-left: auto;
      width: 100%;
      max-width: 100px;
      font-size: 10px;
      line-height: 10px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: auto;
      p{
        text-align: right;
        font-size: 5px;
        line-height: 6px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 100px;
      height: 100px;
      top: 3px;
      left: 50%;
      transform: translateX(-59%) rotate(-90deg);
    }
    .banner__img{
      width: 131px;
      top: -3px;
      left: -16px;
    }

  }
}
