/*banner rcy-728_90*/
.banner{
  &__rcy-728_90{
    width: 728px;
    height: 90px;
  }

  &__body-rcy-728_90{
    padding-top:18px;
    padding-left: 100px;
    padding-right: 10px;
    padding-bottom: 6px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 28px;
      line-height: 26px;
      width: 100%;
      max-width: 136px;
    }
    .banner__description{
      margin-left: auto;
      width: 100%;
      max-width: 198px;
      font-size: 14px;
      line-height: 15px;
      margin-right: 70px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: auto;
      p{
        text-align: right;
        font-size: 6px;
        line-height: 7px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 120px;
      height: 120px;
      top: 4px;
      left: 50%;
      transform: translateX(-63%) rotate(-90deg);
    }
    .banner__img{
      width: 160px;
      top: -9px;
      left: -3px;
    }

  }
}
