/*banner rcy-200_300*/
.banner{
  &__rcy-200_300{
    width: 200px;
    height: 300px;
  }
  &__body-rcy-200_300{
    padding-top:16px;
    padding-left: 20px;
    padding-right: 13px;
    padding-bottom: 20px;
    .banner__footnote{
      p{
        font-size: 8px;
        line-height: 9px;
      }
    }
    .banner__title{
      margin-top: 23px;
      font-size: 18px;
      line-height: 15px;
    }
    .banner__description{
      width: 100%;
      max-width: 167px;
      margin-top: 12px;
      font-size: 12px;
      line-height: 13px;
    }
    .banner__container-img{
      width: 160px;
      height: 160px;
      bottom: 5px;
      right: 18px;
    }
    .banner__img{
      width: 195px;
      top: -7px;
      left: -21px;
    }

  }
}
