/*banner rcy-240_600*/
.banner{
  &__rcy-240_600{
    width: 240px;
    height: 600px;
  }
  &__body-rcy-240_600{
    padding-top:20px;
    padding-left: 18px;
    padding-right: 9px;
    .banner__footnote{
      p{
        font-size: 12px;
        line-height: 14px;
        &:last-child{
          font-size: 11px;
        }
      }

    }
    .banner__title{
      margin-top: 65px;
      font-size: 24px;
      line-height: 28px;
    }
    .banner__description{
      width: 100%;
      max-width: 209px;
      margin-top: 20px;
      font-size: 15px;
      line-height: 16px;
    }
    .banner__container-img{
      width: 280px;
      height: 280px;
      bottom: 61px;
      right: -53px;
    }
    .banner__img{
      width: 292px;
      top: -20px;
      left: 2px;
    }
  }
}
