/*banner rcy-640_134*/
.banner{
  &__rcy-640_134{
    width: 640px;
    height: 134px;
  }

  &__body-rcy-640_134{
    //padding-top:20px;
    padding-left: 16px;
    padding-right: 14px;

    .banner__flex-row{
      margin-top: 41px;
    }
    .banner__title{
      font-size: 26px;
      line-height: 28px;
    }
    .banner__description{
      margin-left: auto;
      width: 100%;
      max-width: 209px;
      font-size: 15px;
      line-height: 16px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: 19px;
      p{
        text-align: right;
        font-size: 8px;
        line-height: 9px;
        &:first-child{
          text-transform: uppercase;
        }
      }
    }
    .banner__container-img{
      width: 160px;
      height: 160px;
      top: 2px;
      left: 50%;
      transform: translateX(-52%) rotate(-90deg);
    }
    .banner__img{
      width: 200px;
      top: 6px;
      left: -6px;
    }

  }
}
