/*banner mail-240_400*/
.banner{
  &__mail-240_400{
    width: 240px;
    height: 400px;
  }
  &__body-mail-240_400{
    padding-top:21px;
    padding-left: 20px;
    padding-right: 5px;
    padding-bottom: 15px;

    .banner__footnote{
      p{
        font-size: 9px;
        line-height: 11px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }

    .banner__title{
      margin-top: 36px;
      font-size: 22px;
      line-height: 17px;
    }
    .banner__description{
      width: 100%;
      max-width: 210px;
      margin-top: 16px;
      font-size: 15px;
      line-height: 17px;
    }
    .banner__container-img{
      width: 200px;
      height: 200px;
      bottom: 7px;
      right: 19px;
    }
    .banner__img{
      width: 240px;
      top: -6px;
      left: -24px;
    }
  }
}
