/*banner mail-290_290*/
.banner{
  &__mail-290_290{
    width: 290px;
    height: 290px;
  }
  &__body-mail-290_290{
    padding-top:16px;
    padding-left: 20px;
    padding-right: 16px;
    padding-bottom: 11px;

    .banner__footnote{
      margin-left: auto;
      p{
        text-align: right;
        font-size: 8px;
        line-height: 9px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__footnote-additional{
      margin-top: auto;
      p{
        font-size: 8px;
        line-height: 9px;
      }

    }
    .banner__title{
      margin-top: 27px;
      font-size: 22px;
      line-height: 22px;
    }
    .banner__description{
      width: 100%;
      max-width: 160px;
      margin-top: 16px;
      font-size: 16px;
      line-height: 16px;
    }
    .banner__container-img{
      width: 146px;
      height: 146px;
      bottom: -3px;
      right: 4px;
    }
    .banner__img{
      width: 209px;
      top: -47px;
      left: -11px;
    }
  }
}
