/*banner mail-1280_250*/
.banner{
  &__mail-1280_250{
    width: 1280px;
    height: 250px;
  }

  &__body-mail-1280_250{
    padding-top:54px;
    padding-left: 40px;
    padding-right: 13px;
    padding-bottom: 10px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 58px;
      line-height: 56px;
    }
    .banner__description{
      width: 100%;
      max-width: 600px;
      font-size: 32px;
      line-height: 32px;
      margin-top: 24px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: -7px;
      p{
        text-align: right;
        font-size: 14px;
        line-height: 16px;
        &:nth-child(2){
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 280px;
      height: 280px;
      top: 12px;
      left: 50%;
      transform: translateX(20%) rotate(-90deg);
    }
    .banner__img{
      width: 330px;
      top: -12px;
      left: 1px;
    }

  }
}
