/*banner rcy-970_250*/
.banner{
  &__rcy-970_250{
    width: 970px;
    height: 250px;
  }

  &__body-rcy-970_250{
    padding-top:69px;
    padding-left: 40px;
    padding-right: 16px;
    padding-bottom: 14px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 58px;
      line-height: 52px;
      width: 100%;
      max-width: 280px;
    }
    .banner__description{
      margin-left: auto;
      width: 100%;
      max-width: 270px;
      font-size: 27px;
      line-height: 28px;
      margin-right: 35px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: auto;
      p{
        text-align: right;
        font-size: 10px;
        line-height: 10px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 240px;
      height: 240px;
      top: 19px;
      left: 50%;
      transform: translateX(-55%) rotate(-90deg);
    }
    .banner__img{
      width: 346px;
      top: -18px;
      left: -40px;
    }

  }
}
