/*banner rcy-160_600*/
.banner{
  &__rcy-160_600{
    width: 160px;
    height: 600px;
  }
  &__body-rcy-160_600{
    padding-top:16px;
    padding-left: 13px;
    padding-right: 3px;
    padding-bottom: 20px;
    .banner__footnote{
      p{
        font-size: 8px;
        line-height: 9px;
        &:last-child{
          font-size: 7px;
          line-height: 8px;
        }
      }
    }
    .banner__title{
      margin-top: 48px;
      font-size: 22px;
      line-height: 24px;
    }
    .banner__description{
      width: 100%;
      max-width: 145px;
      margin-top: 26px;
      font-size: 15px;
      line-height: 16px;
    }
    .banner__container-img{
      width: 259px;
      height: 259px;
      bottom: 80px;
      right: -111px;
    }
    .banner__img{
      width: 229px;
      top: 2px;
      left: 13px;
    }

  }
}
