
.banner{
  position: relative;
  overflow: hidden;
  height: 100%;
  &>a{
    text-decoration: none;
    color: #1c1b28;
  }
  &__border{
    border: 1px solid #000;
  }
  &__body{
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  &__flex-row{
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  &__footnote{
    p{
      letter-spacing: -0.015em;
      color: #686D82;
      font-family: $ff-medium;
    }
  }
  &__footnote-additional{
    p{
      letter-spacing: -0.015em;
      color: #686D82;
      font-family: $ff-medium;
    }
  }
  &__title{
    position: relative;
    z-index: 1;
    font-family: $ff-bold;
    color: #1c1b28;
    letter-spacing: -0.015em;
    span{
      color: #6659B1
    }
  }
  &__description{
    color: #1C1B28;
    font-family: $ff-medium;
    letter-spacing: -0.015em;
  }
  &__container-img{
    position: absolute;
    border-radius: 50%;
    background: linear-gradient(51.11deg, rgba(123, 97, 255, 0) 3.41%, rgba(123, 97, 255, 0.055) 29.89%, rgba(109, 82, 245, 0.55) 102.04%);
    transform: rotate(-90deg);
  }
  &__img{
    transform: rotate(90deg);
    position: absolute;
  }
}
.container-banners{
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
   .banner{
     margin-bottom: 30px;
   }
}
