/*banner rcy-240_400*/
.banner{
  &__rcy-240_400{
    width: 240px;
    height: 400px;
  }
  &__body-rcy-240_400{
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 11px;
    .banner__footnote{
      p{
        font-size: 9px;
        line-height: 11px;
      }

    }
    .banner__title{
      margin-top: 44px;
      font-size: 22px;
      line-height: 17px;
    }
    .banner__description{
      margin-top: 16px;
      font-size: 15px;
      line-height: 17px;
    }
    .banner__container-img{
      width: 200px;
      height: 200px;
      bottom: 6px;
    }
    .banner__img{
      width: 240px;
      top: -1px;
      left: -20px;
    }

  }
}
