/*banner rcy-320_480*/
.banner{
  &__rcy-320_480{
    width: 320px;
    height: 480px;
  }
  &__body-rcy-320_480{
    padding-top:20px;
    padding-left: 20px;
    padding-right: 5px;
    padding-bottom: 10px;
    .banner__footnote{
      p{
        font-size: 12px;
        line-height: 14px;
      }
    }
    .banner__title{
      margin-top: 40px;
      font-size: 27px;
      line-height: 28px;
    }
    .banner__description{
      width: 100%;
      max-width: 265px;
      margin-top: 12px;
      font-size: 19px;
      line-height: 21px;
    }
    .banner__container-img{
      width: 260px;
      height: 260px;
      bottom: 8px;
      right: 29px;
    }
    .banner__img{
      width: 259px;
      top: 9px;
      left: -9px;
    }

  }
}
