/*banner rcy-640_100*/
.banner{
  &__rcy-640_100{
    width: 640px;
    height: 100px;
  }

  &__body-rcy-640_100{
    padding-top:24px;
    padding-left: 40px;
    padding-right: 15px;
    padding-bottom: 6px;

    .banner__flex-row{
      //margin-top: 41px;
    }
    .banner__title{
      font-size: 28px;
      line-height: 28px;
      width: 100%;
      max-width: 136px;
    }
    .banner__description{
      margin-left: auto;
      width: 100%;
      max-width: 220px;
      font-size: 16px;
      line-height: 17px;
      margin-right: 31px;
    }
    .banner__footnote{
      margin-left: auto;
      margin-top: auto;
      p{
        text-align: right;
        font-size: 6px;
        line-height: 7px;
        &:last-child{
          span{
            text-transform: uppercase;
          }
        }
      }
    }
    .banner__container-img{
      width: 120px;
      height: 120px;
      top: 5px;
      left: 50%;
      transform: translateX(-83%) rotate(-90deg);
    }
    .banner__img{
      width: 144px;
      top: 10px;
      left: 3px;
    }

  }
}
